import React, { useEffect, useState } from "react";
import {
  Row,
  Col,
  Card,
  Form,
  DatePicker,
  Empty,
  Button,
  Input,
  Pagination,
  Drawer,
  Spin,
  List,
  Popconfirm,
} from "antd";
import {
  FilterOutlined,
  UploadOutlined,
  PlusOutlined,
  OrderedListOutlined,
  AppstoreOutlined,
} from "@ant-design/icons";
import { connect } from "react-redux";
import moment from "moment";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import PageTitle from "../../../../layout/components/content/page-title";
import EmptyImage from "../../../../assets/images/app/empty-list.svg";
import {
  InputChangeValue,
  clearData,
  getMyStockList,
  setMyStockParams,
  clearStockList,
} from "../../../../store/sideMenu/mystock/mystockSlice";
import MyStockGrid from "./myStockGrid";
import MyStockList from "./myStockList";
import Filter from "./filter";
import { getReportList } from "../../../../store/reports/reportsSlice";
import { RiErrorWarningLine } from "react-icons/ri";

function MyStock(props) {
  const [form] = Form.useForm();
  const { Search } = Input;
  const [productListToggle, setProductListToggle] = useState(false);
  const [isShown, setIsShown] = useState(false);
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";

  useEffect(() => {
    props.clearData();
    props.getMyStockList({
      ...props.myStockParams,
      page: 1,
      search: "",
      page_size: 10,
    });
  }, []);

  const handleClick = () => {
    //  toggle shown state
    setIsShown((current) => !current);

    //  or simply set it to true
    // setIsShown(true);
  };

  function onSearch(val) {
    props.getMyStockList({ ...props.myStockParams, page: 1, search: val });
    props.setMyStockParams({ ...props.myStockParams, page: 1, search: val });
  }

  function onPageChange(page, pageSize) {
    props.clearStockList();
    props.setMyStockParams({
      ...props.myStockParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getMyStockList({
      ...props.myStockParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.myStockParams?.start_date,
      end_date: props.myStockParams?.end_date,
    });
  }, [props.myStockParams]);

  function exportData() {
    // var { file_format, ...params } = props.reportParamsData;
    var objData = {
      file_format: 0,
      model_name: "Stock",
    };

    props.getReportList({
      objData,
      ParamsData: {
        ...props.myStockParams,
        start_date:
          props.myStockParams.start_date != null &&
          props.myStockParams.start_date != ""
            ? moment(props.myStockParams.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          props.myStockParams.end_date != null &&
          props.myStockParams.end_date != ""
            ? moment(props.myStockParams.end_date)
                .add(1, "days")
                .format("YYYY-MM-DD")
            : "",
      },
    });
  }

  return (
    <Row gutter={[12, 12]}>
      <PageTitle pageTitle="My Stock" goBack={false} />
      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          <Col md={14} xs={24} className="hp-ecommerce-app-header-search ">
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getMyStockList({
                    ...props.myStockParams,
                    page: 1,
                    search: "",
                  });
                  props.setMyStockParams({
                    ...props.myStockParams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>

          <Col md={4} xs={8}>
            <Button
              // onClick={() => {
              //   props.isDrawerVisible(true);
              // }}
              onClick={handleClick}
              className="hp-w-100 hp-p-12 "
              icon={<FilterOutlined className="hp-mr-10" />}
              size="small"
            >
              {" "}
              Filter
            </Button>
          </Col>

          {props.userAccessList?.indexOf("Purchase.export_stock") !== -1 && (
            <Col md={4} xs={8}>
              <Popconfirm
                placement="topRight"
                title="Are you sure to export this Record?"
                onConfirm={() => exportData()}
                okText="Yes"
                cancelText="No"
                className="hp-ml-8"
                icon={
                  <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
                }
              >
                <Button
                  className="hp-w-100 hp-p-12"
                  icon={<UploadOutlined className="hp-mr-10" />}
                  size="small"
                >
                  Export
                </Button>
              </Popconfirm>
            </Col>
          )}

          <Col md={1} xs={4}>
            <Button
              className="hp-w-100 hp-p-12"
              icon={<AppstoreOutlined />}
              onClick={() => setProductListToggle(false)}
            ></Button>
          </Col>

          <Col md={1} xs={4}>
            <Button
              className="hp-w-100 hp-p-12"
              icon={<OrderedListOutlined />}
              onClick={() => setProductListToggle(true)}
            ></Button>
          </Col>
        </Row>
      </Col>

      {isShown && (
        <Col span={24} xs={24}>
          <Filter />
        </Col>
      )}

      <Col
        span={24}
        xs={24}
        className="hp-purchase-app-content"
        productListToggle={productListToggle}
        setProductListToggle={setProductListToggle}
      >
        <Spin tip="Loading..." spinning={props.loading} delay={500}>
          {props.myStockList.length !== 0 ? (
            <>
              {!productListToggle ? (
                <>
                  <List
                    dataSource={props.myStockList}
                    renderItem={(value) => <MyStockGrid value={value} />}
                  />
                  <Pagination
                    size="small"
                    current={props.myStockParams?.page}
                    pageSize={props.myStockParams?.page_size}
                    total={props.listCount}
                    showTotal={showTotal}
                    showSizeChanger
                    onChange={onPageChange}
                    className="hp-mb-12 hp-mt-24 hp-float-right"
                  />
                </>
              ) : (
                <>
                  <MyStockList
                    stockList={props.myStockList}
                    // deletePurchase={deleteRecord}
                  />{" "}
                  <Pagination
                    size="small"
                    current={props.myStockParams?.page}
                    pageSize={props.myStockParams?.page_size}
                    total={props.listCount}
                    showTotal={showTotal}
                    showSizeChanger
                    onChange={onPageChange}
                    className="hp-mb-12 hp-mt-24 hp-float-right"
                  />
                </>
              )}
            </>
          ) : (
            <Card>
              <Empty
                className="hp-m-32"
                image={EmptyImage}
                imageStyle={{
                  height: 160,
                }}
                description={<h5>Your Stock list is empty</h5>}
              >
                {/* <Button type="primary">
                <Link to=" ">Go to Purchase list</Link>
              </Button> */}
              </Empty>
            </Card>
          )}
        </Spin>
      </Col>
    </Row>
  );
}

function mapStateToProps({ mystock, system }) {
  return {
    loading: mystock.loading,
    myStockParams: mystock.myStockParams,
    myStockList: mystock.myStockList,
    listCount: mystock.listCount,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  InputChangeValue,
  clearData,
  getMyStockList,
  setMyStockParams,
  getReportList,
  clearStockList,
})(MyStock);
